img {
  max-width: 100%; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  padding-top: 29px;
  background-color: transparent; }
  @media only screen and (max-width: 1199px) {
    .page-header {
      padding-top: 39px; } }
  @media only screen and (max-width: 767px) {
    .page-header {
      padding-top: 18px; } }

.page-header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 84.6%;
  margin-left: auto;
  margin-right: auto; }
  @media only screen and (max-width: 1199px) {
    .page-header__container {
      width: 88%; } }
  @media only screen and (max-width: 767px) {
    .page-header__container {
      width: 90%; } }

.page-header__logo {
  display: block; }
  .page-header__logo svg {
    width: auto;
    height: 20px;
    fill: #ffffff; }
    @media only screen and (max-width: 1199px) {
      .page-header__logo svg {
        height: 18px; } }
    @media only screen and (max-width: 767px) {
      .page-header__logo svg {
        height: 15px; } }

.page-header__link {
  font-family: "Bebas Neue", "Helvetica", "Arial", sans-serif;
  font-size: 26px;
  color: #ffffff;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: 0.03em;
  display: block;
  text-decoration: none; }
  @media only screen and (max-width: 1199px) {
    .page-header__link {
      font-size: 24px;
      letter-spacing: 0.02em; } }
  @media only screen and (max-width: 767px) {
    .page-header__link {
      font-size: 20px; } }

.page-section__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 85%;
  margin-left: auto;
  margin-right: auto; }
  @media only screen and (max-width: 1199px) {
    .page-section__container {
      width: 88%; } }
  @media only screen and (max-width: 1199px) {
    .page-section__container {
      width: 91%; } }

.intro {
  padding-top: 50px;
  padding-bottom: 49px;
  background: url("../img/svg/header-bg.svg") center no-repeat;
  background-size: cover;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg); }
  @media only screen and (max-width: 1199px) {
    .intro {
      padding-top: 82px;
      padding-bottom: 78px; } }
  @media only screen and (max-width: 767px) {
    .intro {
      padding-top: 91px;
      padding-bottom: 80px; } }

.intro__content {
  width: 82.75%; }
  @media only screen and (max-width: 1199px) {
    .intro__content {
      width: 98.5%; } }
  @media only screen and (max-width: 767px) {
    .intro__content {
      width: 100%; } }

.intro__social-list {
  align-self: flex-end;
  margin-bottom: 45px;
  padding-left: 0;
  padding-right: 0;
  list-style: none; }

.intro__social-link {
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  font-size: 12px;
  color: #fff;
  font-weight: 400;
