.hero {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: nowrap; }
  @media only screen and (max-width: 1199px) {
    .hero {
      align-items: flex-start; } }

.hero__text {
  display: flex;
  flex-direction: column;
  width: 41.3%;
  padding-top: 12.3%; }
  @media only screen and (max-width: 1199px) {
    .hero__text {
      width: 48.7%;
      padding-top: 7.5%; } }
  @media only screen and (max-width: 767px) {
