        font-size: 24.6vw; } }

.hero__group {
  position: relative; }

.hero__list {
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  font-size: 1.03vw;
  color: #fff;
  font-weight: 400;
  line-height: 150%;
