  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  border: 4px solid #ffffff; }
  @media only screen and (max-width: 1199px) {
    .hero__btn {
      font-size: 1.55vw;
      margin-top: 49px;
      padding: 2.5vw 4vw; } }
  @media only screen and (max-width: 767px) {
